html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
body {
  line-height: 1.6;
  font-family: -apple-system-font, "Helvetica Neue", sans-serif;
}
* {
  margin: 0;
  padding: 0;
}
a img {
  border: 0;
}
a {
  text-decoration: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
/** env = windows **/
::-webkit-input-placeholder {
  font-family: -apple-system-font, "Helvetica Neue", sans-serif;
}
/** prevent default menu callout **/
a {
  -webkit-touch-callout: none;
}

ul,
li {
  list-style: none;
}
#index .ul-items {
  width: 100%;
  position: absolute;
  top: 46px;
  bottom: 41px;
  left: 0;
  overflow: auto;
}
#index .item {
  position: relative;
  border-bottom: 1px solid #ddd;
}
#index .item div {
  line-height: 28px;
  vertical-align: middle;
}
#index .item-time {
  display: block;
  height: 28px;
  line-height: 28px;
  position: absolute;
  right: 20px;
  top: 0;
  bottom: 0;
  margin: auto;
}
#index .paddingLeft {
  padding-left: 20px;
}
#index .edit-check {
  width: 30px;
  height: 100%;
  position: absolute;
}
#index .edit-check input {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}
#index .checkitem {
  -moz-appearance: none;
       appearance: none;
  -webkit-appearance: none;
  width: 20px;
  height: 20px;
  border: 2px solid #999;
  border-radius: 50%;
}
#index .checkitem:checked {
  background: blue;
}
#index .content {
  text-indent: 16px;
  font-size: 12px;
}
#index .authenName {
  font-weight: bold;
}
#index .authenCode {
  font-size: 28px;
  color: blue;
  font-weight: bold;
}
#index .darkred {
  color: #963f3f;
}
footer {
  position: fixed;
  background: #fff;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 40px;
  border-top: 1px solid #ededed;
  line-height: 40px;
  text-align: center;
}
